"use client"

import { useEffect, useState } from "react"
import Api from "../services/logic"
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
}  from "@/components/ui/table"
import { Button } from "@/components/ui/button"
import { Edit, Trash } from "lucide-react"

interface Transaction {
  id: number
  amount: number
  date: string
  category: string
  description: string
  type: "expense" | "income" | string
}

interface Category {
  id: number
  name: string
}

interface SortOptions {
  field: string | null
  direction: "asc" | "desc" | null
}

const api = new Api()

interface TransactionsTableProps {
  onEdit: (transaction: Transaction) => void
}

export default function TransactionsTable({ onEdit }: TransactionsTableProps) {
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [categories, setCategories] = useState<Category[]>([])
  const [loading, setLoading] = useState(false)
  const [page, setPage] = useState(1)
  const [pageSize, setPageSize] = useState(10)
  const [totalPages, setTotalPages] = useState(1)
  const [sort, setSort] = useState<SortOptions>({ field: null, direction: null })
  const [filters, setFilters] = useState<{ category?: string; type?: string; description?: string }>({})

  useEffect(() => {
    async function fetchCategories() {
      const cats = await api.getAllCategories()
      setCategories(Array.isArray(cats) ? cats : [])
    }
    fetchCategories()
  }, [])

  useEffect(() => {
    fetchTransactions()
  }, [page, pageSize, sort, filters])

  async function fetchTransactions() {
    setLoading(true)
    try {
      const result = await api.getTransactions({
        page,
        limit: pageSize,
        sort: sort.field
          ? { field: sort.field, direction: sort.direction || "asc" }
          : undefined,
        filters,
      })

      // Assuming API response has { data, pages }
      setTransactions(result.data || [])
      setTotalPages(result.pages || 1)
    } finally {
      setLoading(false)
    }
  }

  function handleSort(field: string) {
    let direction: "asc" | "desc" | null = "asc"
    if (sort.field === field) {
      if (sort.direction === "asc") direction = "desc"
      else if (sort.direction === "desc") direction = null
      else direction = "asc"
    }
    setSort({ field: direction ? field : null, direction })
    setPage(1)
  }

  function getCategoryName(id: string | number): string {
    return categories.find((cat) => cat.id === Number(id))?.name ?? "Unknown"
  }

  async function handleDelete(id: number) {
    if (!confirm("Удалить операцию?")) return
    await api.deleteTransaction(id)
    fetchTransactions()
  }

  return (
    <div className="overflow-x-auto">
      <Table className="min-w-full">
        <TableCaption>Список операций</TableCaption>
        <TableHeader>
          <TableRow>
            <TableHead className="cursor-pointer" onClick={() => handleSort("date")}>
              Дата {sort.field === "date" ? (sort.direction === "asc" ? "▲" : "▼") : ""}
            </TableHead>
            <TableHead>Категория</TableHead>
            <TableHead>Описание</TableHead>
            <TableHead>Тип</TableHead>
            <TableHead className="text-right cursor-pointer" onClick={() => handleSort("amount")}>
              Сумма {sort.field === "amount" ? (sort.direction === "asc" ? "▲" : "▼") : ""}
            </TableHead>
            <TableHead>Действия</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {!loading && transactions.length === 0 && (
            <TableRow>
              <TableCell colSpan={6} className="text-center">
                Нет операций
              </TableCell>
            </TableRow>
          )}

          {loading &&
            <TableRow>
              <TableCell colSpan={6} className="text-center">
                Загрузка...
              </TableCell>
            </TableRow>}

          {transactions.map((op) => (
            <TableRow key={op.id} className="group">
              <TableCell>{new Date(op.date).toLocaleDateString("ru-RU")}</TableCell>
              <TableCell>{getCategoryName(op.category)}</TableCell>
              <TableCell>{op.description}</TableCell>
              <TableCell>{op.type === "expense" || op.type === "expence" ? "Расход" : "Доход"}</TableCell>
              <TableCell className="text-right">
                {(op.type === "expense" || op.type === "expence" ? "-" : "+") +
                  Math.abs(op.amount).toFixed(2) + " BYN"}
              </TableCell>
              <TableCell>
                <div className="flex items-center gap-2">
                  <Button
                    variant="ghost"
                    size="icon"
                    aria-label="Редактировать"
                    onClick={() => onEdit(op)}
                    className="opacity-0 group-hover:opacity-100 transition"
                  >
                    <Edit className="w-4 h-4" />
                  </Button>
                  <Button
                    variant="ghost"
                    size="icon"
                    aria-label="Удалить"
                    onClick={() => handleDelete(op.id)}
                    className="opacity-0 group-hover:opacity-100 transition"
                  >
                    <Trash className="w-4 h-4 text-red-600" />
                  </Button>
                </div>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>

      {/* Pagination controls */}
      <div className="flex items-center justify-between mt-4">
        <div>
          <label htmlFor="pageSize" className="mr-2">Показывать по:</label>
          <select
            id="pageSize"
            value={pageSize}
            onChange={(e) => {
              setPageSize(Number(e.target.value))
              setPage(1)
            }}
            className="border border-gray-300 rounded px-2 py-1"
          >
            {[10, 20, 50].map((size) => (
              <option key={size} value={size}>
                {size} операций
              </option>
            ))}
          </select>
        </div>

        <div className="flex gap-2">
          <Button
            disabled={page === 1}
            onClick={() => setPage((p) => Math.max(1, p - 1))}
          >
            Предыдущая
          </Button>
          <div className="flex items-center space-x-1">
            {Array.from({ length: totalPages }, (_, i) => (
              <Button
                key={i + 1}
                variant={page === i + 1 ? "default" : "outline"}
                onClick={() => setPage(i + 1)}
              >
                {i + 1}
              </Button>
            ))}
          </div>
          <Button
            disabled={page === totalPages}
            onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
          >
            Следующая
          </Button>
        </div>
      </div>
    </div>
  )
}
