interface Transaction {
  // Define properties of an operation here
  id?: number;
  [key: string]: any;
}

interface SortOptions {
  field: string;
  direction: 'asc' | 'desc';
}

interface Params {
  page?: number;
  limit?: number;
  filters?: Record<string, any>;
  sort?: SortOptions;
}

class Api {
  #baseURL = "http://localhost:3000";
  #categoriesCache: any[] | null = null;

  async #fetch(path: string, options?: RequestInit): Promise<any> {
    const response = await fetch(this.#baseURL + path, options)
    if (!response.ok) {
      // You can read plain text error message here
      const text = await response.text()
      throw new Error(`HTTP error ${response.status}: ${text}`)
    }
    // This assumes a JSON response if status OK
    return await response.json()
  }

  getAllCategories = async (): Promise<any[] | null> => {
    if (!this.#categoriesCache) {
      this.#categoriesCache = await this.#fetch("/categories");
    }
    return this.#categoriesCache;
  };

  createTransaction = (transaction: Transaction): Promise<any> => {
    // Should the cache always be cleared? Review if necessary
    this.#categoriesCache = null;
    return this.#fetch("/transactions", { method: "POST", body: JSON.stringify(transaction) });
  };

  deleteTransaction = (id: number | string): Promise<any> =>
    this.#fetch(`/transactions/${id}`, { method: "DELETE" });

  updateTransaction = (id: number | string, updates: Partial<Transaction>): Promise<any> =>
    this.#fetch(`/transactions/${id}`, { method: "PATCH", body: JSON.stringify(updates) });

  async getTransactions(params: Params = {}): Promise<any> {
    const { page = 1, limit = 10, filters = {}, sort = { field: '', direction: 'asc' } } = params;
    const query = new URLSearchParams({ _page: page.toString(), _per_page: limit.toString(), ...filters });

    if (sort.field) {
      query.set("_sort", sort.direction === "desc" ? `-${sort.field}` : sort.field);
    }

    const result = await this.#fetch(`/transactions?${query.toString()}`);
    return { ...result, currentPage: page };
  }
}

export default Api;
